body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header {
    padding: 15px 0 15px 0;
    background-color: #333;
    position: fixed;
    overflow: hidden;
    top: 0;
    width: 100%;
}

.header > a {
    padding: 15px 10px 15px 5px;
    color: white;
    text-align: center;
    text-decoration: none;

}

.header > a:hover:not(.active) {
    background-color: #111;
}

@media only screen and (max-width: 768px) {
    .header {
        padding: 0;
    }
    .header > a {
        display: block;
    }
}
input[type=email], input[type=password] {
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
}

input[type=submit] {
    width: 100%;
    background-color: #4CAF50;
    color: white;
    padding: 14px 20px;
    margin: 22px 0;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

input[type=submit]:hover {
    background-color: #45a049;
}

#loginForm {
    width: 500px;
    height: 370px;
    margin: 70px auto;
    border-radius: 5px;
    background-color: #f2f2f2;
    padding: 20px;
}
.alert {
    padding: 20px;
    background-color: #f44336;
    color: white;
    opacity: 1;
    transition: opacity 0.6s;
    margin-bottom: 15px;
}

.alert.success {background-color: #4CAF50;}
.alert.info {background-color: #2196F3;}
.alert.warning {background-color: #ff9800;}

.closebtn {
    margin-left: 15px;
    color: white;
    font-weight: bold;
    float: right;
    font-size: 22px;
    line-height: 20px;
    cursor: pointer;
    transition: 0.3s;
}

.closebtn:hover {
    color: black;
}
input[type=text] {
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
}

input[type=submit] {
    width: 100%;
    background-color: #0099FF;
    color: white;
    padding: 14px 20px;
    margin: 22px 0;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

input[type=submit]:hover {
    background-color: #0066FF;
}

#createWordForm {
    width: 500px;
    height: 370px;
    margin: 70px auto;
    border-radius: 5px;
    background-color: #f2f2f2;
    padding: 20px;
}
* {
    box-sizing: border-box;
}

.active {
    background-color: #111;
}

.container::after {
    content: "";
    clear: both;
    display: block;
}

.hidden {
    display: none;
}
